/* Quick Search Css*/

#quicksearch{
  width:80%;
  height: 500px;
  margin-left:10%;
  margin-top:2%
}

#QuickHeading{
  color:#192f60;
  font-size:30px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  display:block;
  font-weight:bold;
  text-align: left;   
}

#QuickSubHeading{
  color:gray;
  font-size:20px;
  margin-top: 20px;
  position: absolute;
  font-family: Georgia, 'Times New Roman', Times, serif;
  line-height: .01;
}

.tileContainer{
  height: 180px;
  width: 30%;
  -webkit-box-shadow: 0 0 8px 8px grey;
  -moz-box-shadow: 0 0  8px grey;
  box-shadow: 0 5px 15px black;
  background-color: white;
  display: inline-block;
  margin-left: 2%;
  margin-top: 4%;
  
}

#mainTileContainer{
  margin-top: 3%;
}

.tileComponent1{
  width:46%;
  float: left;
}

.tileComponent2{
  width:46%;
  float: left;
  margin-left:20px;
  margin-top: 20px;
}

.tileComponent1 > img{
  height: 180px;
  width:100%
}

.componentHeading{
  color: #192f60;
  font-size:25px;
  font-weight: bold
}

.componentSubHeading{
  color: gray;
  font-size:18px;
  line-height:1;
}

.tileContainer:hover{
 transform: scale(1.02)
}

@media screen and (max-width:1200px){
  .tileContainer{
      width: 40%;
  }
}

@media screen and (max-width:1000px){
  .tileContainer{
      width: 40%;
      
  }
  .componentSubHeading{
      color:rgb(230, 41, 41);
  }
}

@media screen and (max-width:800px){
  .tileContainer{
      width: 80%;
  }
}

@media screen and (max-width:600px){
  .tileContainer{
      width: 100%;
  }
  #mainTileContainer{
      margin-top:7%;
  }
}


.input-box {
  font-size: 14px;
  padding-left: 35px;
  width: 487px;
  height: 55px;
  color: #8c96ab;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  border: none;
  margin-left: -14px;
}

.search {
  left: 10px;
  color: #8c96ab;
  font-size: 14px;
}

.search-result-restaurant-block{
  background-color: #fff;
  text-align: left;
  cursor: pointer;
  display:none
}
.search-result-image-block{
  display: inline-block;
  margin: 10px 0 10px 20px;
  
}
.search-result-restaurant-details{
  display: inline-block;
  vertical-align: top; 
  margin: 10px 0 10px 20px;
}
.search-result-restaurant-name{
  color: #192f60;
  font-size: 16px;
  font-weight: bold;
}
.search-result-restaurant-address{
  color: #636f88;
  font-size: 12px;
  font-weight: normal; 
  width: 300px;
}
.no-result-block{
  background-color: #fff;
  text-align: left;
  height: 50px;
  padding: 10px;
}
.no-result-message{
  color: #192f60;
  font-size: 16px;
  font-weight: bold;
}


#box{
  border:2px solid black;
  height:100px;
  width:200px;
  margin:10%
}

#box>p{
  margin:20%
}

#mypara1{
  border:1px solid green;
  padding:10px;
}

#mypara2{
  border:1px solid orange;
  margin:10px;
}